<template>
  <div class="wait_create_case">
    <listSearch 
      v-model="searchFormData"
      @searchConfirm="goSearch"
      :searchArr="searchArr">
    </listSearch>
    <div class="content">
      <div class="buttons">
        <Button type="primary" @click="btnOperate(3)">批量呈报</Button>
        <Button type="primary" @click="btnOperate(4)" style="margin-left: 8px">批量驳回</Button>
      </div>
      <div class="table_box mgt20">
        <Table
          @on-select="selectOne"
          @on-select-cancel="selectCancel"
          @on-select-all="selectAll"
          @on-select-all-cancel="selectAllCancel"
          :columns="columns"
          :data="tableData"
          show-sizer
          show-elevator
          show-total>
          <template slot-scope="{ row }" slot="proxyName">
            <span>{{ row.proxyName }}</span>
          </template>
          <template slot-scope="{ row }" slot="state">
            <span>{{ caseStatus[row.state] }}</span>
          </template>
          <template slot-scope="{ row }" slot="action">
             <a href="javascript:;" style="margin-right:20px" @click="btnOperate(1,row)" title="立案呈报">
               <img width="26" height="26" src="../../../assets/images/dailian.png" alt />
              </a>
              <a href="javascript:;" @click="btnOperate(2,row)" title="立案驳回">
                <img width="26" height="26" src="../../../assets/images/reject.png" alt />
              </a>
          </template>
        </Table>
        <!-- 分页 -->
  
        <Page class="mgt20"
         :current="currentPage"
          :total="total"
          :page-size="pageSize"
          :page-size-opts="pageOpts"
          @on-change="changeCurrentPage"
          @on-page-size-change="changeSize"
          show-sizer
          show-elevator
          show-total/>
      </div>
    </div>
    <!-- 弹窗部分 -->
    <popupBox :show="popupShow" 
      :titleText="popupTypeObj[popupType]['title']"
      :width="'740'"
      @popupConfirm="popupConfirm" 
      @popupCancel="popupCancel"
      >
      <!-- 驳回 -->
      <div class="reject_content" v-if="popupType == 2 || popupType == 4">
        <div class="title">请输入驳回原因：</div>
        <textarea v-model="rejectReason"  name="" id="" cols="30" rows="10"></textarea>
      </div>
      <!-- 受理 -->
      <div class="accept_content" v-if="popupType == 1 || popupType == 3">
        <div class="item">
          <div class="item_content">
            <div class="label">请选择纠纷类型：</div>
            <div class="val">
              <Select style="width: 200px" v-model="disputeType">
                <Option
                v-for="item in caseTypes" 
                :key="item.id"
                :value="item.itemValue"
                >{{item.item}}</Option>
              </Select>
            </div>
          </div>
        </div>
        
      </div>
    </popupBox>
  </div>
</template>

<script>
import listSearch from '@/components/list-search/search.vue'
import {listMixin} from '../listMixin'
// import zzlist from '@/components/zzlist/zzlist'
import {mapActions} from 'vuex'
const columns = [
        {
          type: 'selection',
          width: 100,
          align: 'center',
        },
        {
          title: '案件编号',
          key: 'id',
          align: "center",
          width:200,
        },
        {
          title: '案件类型',
          key: 'caseTypeName',
          align: "center"
        },
        {
          title: '申请人',
          key: 'applicantName',
          align: "center"
        },
        {
          title: '代理人',
          key: 'proxyName',
          slot:"proxyName",
          align: "center"
        },
        {
          title: '被申请人',
           key: "respondentName",
          align: "center"
        },
        {
          title: '提交时间',
          key: "createTime",
          align: "center",
          width: 200
        },
        {
          title: '受理时间',
           key: 'acceptTime',
           align: "center",
        },
        {
          title: '案件状态',
          key: 'state',
          slot:"state",
          align: "center"
        },
        {
          title: '操作',
          slot: 'action'
        }
      ]
export default {
  name: 'wait_create_case',
  components: { listSearch },
  mixins: [listMixin],
  data () {
    return {
      popupShow: false,
      popupType: 4,
      popupTypeObj: {
        1: {type:1, title: '确认立案'},
        2: {type:2, title: '案件驳回（单案）'},
        3: {type:3, title: '确认立案'},
        4: {type:4, title: '案件驳回（批量）'},
      },
      rejectReason: null,
      disputeType: null,
      searchFormData: {
        personalName: null,
        companyName: null,
        checkbox: [],
        radio: null,
        status: null,
        datePeriod: []
      },
      searchArr: [
        {type: 'input', value: 'personalName', label: '案件编号', placeholder: '请输入个人姓名'},
        {type: 'input', value: 'companyName', label: '申请人', placeholder: '请输入企业名称'},
        {type: 'input', value: 'companyName', label: '被申请人', placeholder: '请输入企业名称'},
        {type: 'select', value: 'status', label: '注册名称',
         arr: [
          {label: '通过', value: 1},
          {label: '未通过', value: 2}
         ], 
          placeholder: '请选择'
        },
        {type: 'select', value: 'status', label: '案件类型',
         arr: [
          {label: '通过', value: 1},
          {label: '未通过', value: 2}
         ], 
          placeholder: '请选择'
        },
        {
          type: 'datePicker', value: 'datePeriod', label: '提交时间', placeholder: '选择时间'
        }
      ],
      columns: columns,
      caseTypes:[], // 案由
      requestIds:[], // 单个和多个操作的ids
    }
  },
  mounted () {
    this.initCaseTypes()
  },
  methods: {
    ...mapActions({
      getRequsetList:"createCase/getRequsetList",
      waitCaseTypes:"createCase/waitCaseTypes",
      setCaseOne:'createCase/setCaseOne',
      setCaseAll:"createCase/setCaseAll",
      setrejectOne:"createCase/setrejectOne",// 单个驳回
      setrejectAll:"createCase/setrejectAll", // 批量驳回
    }),
    // 搜索
    goSearch () {
      console.log(this.searchFormData)
    },
    // 按钮此作
    btnOperate (type,row) {
      // 1:受理 2:驳回 3:批量受理 4:批量驳回
      console.log(type, row);
       let length = this.selectedData.length;
      if (type == 3 && length === 0) {
        this.$Message.warning("请至少选择一条案件信息");
        return;
      }
      if (type == 1 || type == 2) {
        // 单个受理
        this.requestIds=new Array(row.id);
      }
      if (type == 3 || type == 4) {
        // 批量受理
       this.requestIds= this.selectedData;
      }

      this.popupType = type;
      
      this.popupShow = true
    },
    // 弹窗确认
    popupConfirm () {
      // 1:受理 2:驳回 3:批量受理 4:批量驳回
      console.log("类型", this.popupType);
      if (this.popupType == 1) {
        // 单个
        this.submitOne();
      } else if (this.popupType == 3) {
        // 批量受理
        this.submitAll();
      } else if (this.popupType == 2) {
        // 单个驳回
        this.submitBackOne();
      } else if (this.popupType == 4) {
        // 批量驳回
        this.submitBackAll();
      }
    },
    // 弹窗取消
    popupCancel () {
      console.log(1)
      this.popupShow = false
      this.requestIds=[] // 重置
    },
    async submitAll() {
      let arr = [];
      const selected = this.caseTypes.filter(item=>{
        if(item.itemValue==this.disputeType){
          return item
        }
      })
      this.requestIds.map(item => {
        let obj = {};
        obj["entity_caseId"] = item;
        obj["actionIdentifer"] = "UPCASE_SUBMIT";
        obj['disputeType_name']=selected[0].item|| ''
        obj["disputeType"]=this.disputeType;
        arr.push(obj);
      });
      
      console.log(arr)
      
      const result = await this.setCaseAll({ arr: JSON.stringify(arr) });
      console.log(result, ":批量立案");
      if (result.data.flag) {
        this.$Message.success('操作成功！')
        
        //  重置
        this.popupCancel();
        this.getList()
      } else {
        this.$Message.error(result.data.message);
      }
    },
    async submitOne(){
      let params = {};
      const selected = this.caseTypes.filter(item=>{
        if(item.itemValue==this.disputeType){
          return item
        }
      })
      if (this.requestIds.length == 1) {
        params["entity_caseId"] = this.requestIds[0];
        params["disputeType"] = this.disputeType;
        params['disputeType_name']=selected.length>0? selected[0].item: ''
      }

      const result = await this.setCaseOne({
        actionIdentifer: "UPCASE_SUBMIT",
        ...params
      });

      if (result.data.flag) {
        this.$Message.success('操作成功！')

        //  重置
        this.getList()
        this.popupCancel()
      } else {
        this.$Message.error(result.data.message);
      }
    },
    async submitBackOne(){
      let params = {};
      if (this.requestIds.length == 1) {
        params["entity_caseId"] =this.requestIds[0]
        params["reason"] = this.rejectReason;
        params["actionIdentifer"] = "UPCASE_REJECT";
      }
      
      const result = await this.setrejectOne(params);
      if (result.data.flag) {
        this.$Message.success('操作成功！')

        //  重置
        this.popupCancel();
        this.getList()
      } else {
       this.$Message.error(result.data.message);
      }
    },
    // 批量驳回
    async submitBackAll(){
       let arr = [];
      this.requestIds.map(item => {
        let obj = {};
        obj["entity_caseId"] = item;
        obj["actionIdentifer"] = "UPCASE_REJECT";
        obj["reason"]=this.rejectReason;
        arr.push(obj);
      });
      const result = await this.setrejectAll({ arr: JSON.stringify(arr) });
      console.log(result, ":批量驳回");
      if (result.data.flag) {
         this.$Message.success('操作成功！')
        //  重置
        this.popupCancel();
        this.getList()
      } else {
       this.$Message.error(result.data.message);
      }
    },
    async getList () {
        let params = {
        menuIdentifer: "UPCASE_PRE_LIST", //菜单标识
        pageIndex: this.currentPage - 1,
        pageSize: this.pageSize
      };
      let filter ={//筛选条件
        applicantName:"",//申请人关键字
        respondentName:"",//被申请人关键字
        userId:'' ,//注册名称的用户id
        caseTypeId: '',//案件类型id
    	  createTimeBegin:"",//提交的开始时间
        createTimeEnd:"" //提交的结束时间
    }

      params = { ...params,filter:JSON.stringify(filter) };
      // console.log("参数", params);
      const res= await this.getRequsetList(params)
      if (res.data.flag) {
        console.log('待立案list',res.data.data);
        this.tableData = res.data.data.dataList;
        this.total = res.data.data.totalCount;

        // this.tableAll = this.duplicate(res.data.data.dataList, "id"); // 所有list
      }
    },
    async initCaseTypes(){
      const result= await this.waitCaseTypes()
  
      if (result.data.flag) {
        this.caseTypes=result.data.data
      }
    }
  }
}
</script>
<style lang="less" scoped>
/deep/thead .ivu-table-cell-with-selection::after {
  content: "全选";
  position: relative;
  left: 5px;
}
.wait_create_case {
  .content {
    margin-top: 30px;
    padding: 20px 10px;
    background: #fff;
    border-radius:4px;
    .buttons {
      padding-right: 20px;
      text-align: right;
    }
    .table_box {
      // padding: 0 2px;
    }
  }
  .mgt20 {
    margin-top: 20px;
  }
  .mgl10 {
    margin-left: 10px;
  }
}
.accept_content {
  .item {
    .item_content {
      display: flex;
      margin-bottom: 6px;
      .label {
        width: 50%;
        flex: 1;
        text-align: right;
        font-size: 16px;
        font-family:PingFangSC-Regular,PingFang SC;
        font-weight:400;
        color: #708094;
      }
      .val {
        width: 50%;
        input {
          padding-left: 8px;
          border-radius:6px;
          border:1px solid rgba(156,178,205,1);
          font-size: 16px;
          font-family:PingFangSC-Regular,PingFang SC;
          font-weight:400;
          color: #708094;
        }
        span {
          font-size: 16px;
          font-family:PingFangSC-Regular,PingFang SC;
          font-weight:400;
          color: #708094;
        }
      }
    }
  }
}
</style>
<style lang="less">
@import url('../../../style/iview_table.less');
</style>